
import { Divider } from "antd";
import { styled } from "@stitches/react";
import * as React from "react";
import Lightbox from "yet-another-react-lightbox";
import { RowsPhotoAlbum } from "react-photo-album";
import "react-photo-album/rows.css";
import "yet-another-react-lightbox/styles.css";
const img2 = require('../images/pic2.jpg');


const Wrapper = styled("div", {
  background: "#efebe9",
  backgroundImage: "url(../images/GroovePaper.png)",
  width: "100%",
});

const Title = styled("p", {
  fontSize: "2vh",
  fontWeight: "bold",
  opacity: 0.85,
  marginBottom: 0,
});

const breakpoints = [3840, 1920, 1080, 640, 384, 256, 128];

function assetLink(asset: string, width: number) {
  return `${encodeURIComponent(
    `/wedding-card/static/media/${asset}`
    )}&w=${width}&q=75`
}

export const slides = [
  { asset: "pic2.63350cb21ed78da510c3.jpg", width: 3840, height: 5070 },

].map(({ asset, width, height }) => ({
  src: assetLink(asset,width),
  width,
  height,
  srcSet: breakpoints.map((breakpoint) => ({
    src: assetLink(asset,breakpoint),
    width: breakpoint,
    height: Math.round((height / width) * breakpoint),
  })),
}));


export default function Gallery2() {
  const [index, setIndex] = React.useState(-1);
  return (
    <Wrapper>
      <Divider plain style={{ marginTop: 0, marginBottom: 32 }}>
        <Title>Our beautiful moment</Title>
      </Divider>
      <p>{img2}</p>
      <RowsPhotoAlbum
        photos={slides}
        targetRowHeight={150}
        onClick={({ index: current }) => setIndex(current)}
      />
      <Lightbox
        index={index}
        slides={slides}
        open={index >= 0}
        close={() => setIndex(-1)}
      />
      
    </Wrapper>
  );
}

